@import url(https://fonts.googleapis.com/css?family=Arvo:regular,italic,700,700italic);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,300italic,regular,italic,600,600italic,700,700italic,800,800italic);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {

  --gold: #B2AB11;
  --gray: #C4C4C4;
  --black: #000000;
  --teal: #53CDE0;
  --blue: #49B0F5;
  --red: #EC4723;

/* Default font */
  --default-font: "Open Sans", sans-serif;

/*Icons/Images*/
  --next-section-arrow: url(data:image/png;base64,);
  --code-background: url(/portfolio/static/media/background.3f124737.png);
  --email-logo: url(data:image/png;base64,);
  --github-logo: url(data:image/png;base64,);
  --JH-circle-logo:url(data:image/png;base64,);
  --JH-logo-one: url(data:image/png;base64,);
  --JH-logo-two: url(data:image/png;base64,);
  --linkedIN-logo: ("./assets/LinkedIN.png");
  --social-block: ("./assets/Social.png");
} 



.circleLogo {
  /* z-index: 5; */
  background: rgba(0,0,0,0);
  width: 60px;
  height: 60px;
  margin: 1% auto 2.5% 7%;


}
.Nav_item_wrapper {

white-space: nowrap;
  margin: 3.7% 4.3% 3.7% auto;
  background: rgba(0, 0, 0, 0,.2);

}
.Nav_link {
  color: var(--gold);
  list-style: none;
  z-index:4;
  font-size: 16px;
  font-family: 'Open Sans';
}
.Nav_item {
  opacity: 4;
  display: inline-block;
}
.Header_root {
  z-index: 3;
  opacity: 1;
}
.Header {
  z-index: -1;
  display: flex;
  width: 100%;
  box-sizing: border-box;
  /* opacity: .2; */
  /* background: rgba(0, 0, 0, 0,.2); */
  height: 80px;
  margin-top: 0;
}

.Hi_container {

  background-color: rgba(0, 0, 0, 0);
}

.Hi {
  font-family: 'Arvo';
  display: flex;
  margin: 20% auto auto auto;
  background-color: rgba(0, 0, 0, 0);
  font-size: 80px;
  text-align: left;
}

.Period {
  background-color: rgba(0, 0, 0, 0);
  color: var(--gold);
  
}
.Developer_violinist {
  display: flex;
  font-size: 32px;
  font-family: 'Open Sans';
  background-color: rgba(0, 0, 0, 0);
}
.Landing_br {
  background-color: rgba(0,0,0,0);
    color: var(--gold);
    width: 75px;
    border: 1px solid;
}

.About_blurb {
  font-size: 24px;
  font-family: 'Open Sans';
  background-color: rgba(0,0,0,0);
  line-height: 32px;;
}
.arrow_down {
  display: flex;
  background-color: rgba(0,0,0,0);
  margin: auto auto 1% auto;

}
.LandingPage {
  display: flex-grid;
  flex-direction: column;
  justify-content: flex-start;
  grid-row: 1;
  width: 100%;
  height: 52%;
  max-height: 70%;
  background-image: url(/portfolio/static/media/background.3f124737.png);
  background-size: cover;
  color: wHite;
  margin: -200px -12px auto --12px;
}



.About_text{
    margin-bottom: 10px;
    max-width: 64%
}
.About_image {
    /* max-width: 364px; */
    width: 364px;

    display: inline-block;
    /* max-height:233px; */
    height:233px;

    /* height: 2%; */
    /* margin: 10px 10px auto auto; */
    background-color: var(--gold);
    /* width: 4%; */
}
.About_wrapper{
    box-sizing: border-box;
    display: inline-flex;
    margin-bottom: 10px;
    align-items: center;
    justify-content: space-between;
}

.About_title {
    display: flex;
  font-family: 'Arvo';
    font-size: 60px;
    width: 100%;
    height: 175px;
    align-self: flex-start;

}

.About {
    color: white;
    border: 1px solid red;
    box-sizing: border-box;
    grid-row: 4;
    box-sizing:border-box;
    justify-content: flex-start;
 
}
* {
  margin: 0 12px 0 12px;
  background-color: var(--black);
}

body {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-columns: auto;
  height: 100vh;
  width: 100vw;
  margin: auto;
  /* max-width: 100vw; */
  /* min-width: 500px; */
}


.thumbnail{
  max-width: 25vw;
  height: auto;
  background-color: var(--gold);
}

.ViewCode {
  color: var(--gold);
  font-size: 16px;
  text-align: center;
  
}

.thumbnail_item {
  list-style: none;
  display: flex;
  flex-direction: column;
  text-align: center;
}
.project_wrapper {
  display: flex;
  width: 100vw;
  box-sizing: border-box;
}
.project_thumbnails {
  display: flex;
  /* background-color: red; */
  border-radius: 4px;
  /* max-height: 300px; */
  justify-items:space-evenly;
  margin-bottom: auto 10% 10px 10%;
}
.Portfolio_title {
  display: flex;
  font-family: 'Arvo';
  font-size: 60px;
  width: 100%;
  height: 175px;
}

.Portfolio {
  box-sizing: border-box;
  flex-direction: column;
  display: flex;
  grid-row: 2;
  grid-column: auto;
  background-color: black;
  color: white;
  border: 2px solid white;
  max-width: 100vw;
}

.Languages_title {
    font-size: 60px;
    display: flex;
    font-family: 'Arvo';
}

.Languages_list {
    list-style: none;
    height: 100px;
}
.Languages_wrapper {

    margin: 3.7% 4.3% 3.7%
}
.Languages_item {
    display: inline-block;
}
.slashes {
    list-style: none;
    color: var(--gold);
    display: inline-block;
}
.outer_wrapper {
    box-sizing: border-box;

}
.Languages {
    display: flex;
    flex-direction: column;
    border: 1px solid blue;
    color: white;
    box-sizing: border-box;
    grid-row: 3;
}

.Contact_title {
    font-size: 60px;
    display: flex;
    font-family: 'Arvo';
    /* margin-bottom: 16px; */
}

.Contact_text {
    margin: auto auto 5% 5%;
}
.Resume_button {
    height: 42px;
    width: 220px;
    background-color: black;
    font-size: 16px;
    font-family: 'Open Sans', serif;
    font-weight: bold;
    border-radius: 5px;
    color: var(--gold);
    border: 2px solid #B2AB11;
}


.Contact_button {
    background-color: var(--gold);
    height: 42px;
    width: 220px;
    color: black;
    font-size: 16px;
    font-family: 'Open Sans', serif;
    font-weight: bold;
    border-radius: 5px;
    margin: 5%;
}

.Content_button_wrapper {
    display: flex;
    align-content: center;
}
a {
    color: var(--gold);
    font-style: none;
}
.yellow_box{
    width: 79%;
    height: 236px;
    border: 4px solid #B2AB11;
    box-sizing:border-box;
    margin: 5% 10% 5% 10%;

}
.Contact {
    grid-row: 5;
    box-sizing:border-box;
    border: 1px solid green;
    color: white;
}


.Divider{
    color: var(--gold)
}

.Copywrite {
    color: white;
    margin: 1% auto 1% auto;
    display: flex;    
}

.JHLogo {
    margin: 1% auto auto auto;

}

.Footer_icons {
    display: flex;
    margin: 1% auto auto auto;
}

.Footer {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    border: 1px solid orange;
    justify-items: center;
    margin-bottom: 6%;
}
